<template>
    <div class="header">
      <div class="content-wrapper">
        <div class="avatar">
          <!--不能直接指定src。需要用v-bind，这样才可以去引用头像图片的地址，因为seller.avatar一开始是不存在的-->
          <img width="64" height="64" :src="seller.avatar" >
        </div>
        <div class="content">
          <div class="title">
            <span class="brand"></span>
            <!--通过seller和两个{{}}获取name-->
            <span class="name">{{seller.name}}</span>
          </div>
          <div class="description">
            {{seller.description}}/{{seller.deliveryTime}}分钟送达
          </div>
          <div v-if="seller.supports" class="support">
            <span class="icon"  :class="classMap[seller.supports[0].type]"></span>
            <span class="text">{{seller.supports[0].description}}</span>
          </div>
      </div>
        <div v-if="seller.supports" class="support-count" @click = "showDetail">
          <span  class="count">{{seller.supports.length}}个</span>
          <i class="icon-keyboard_arrow_right"></i>
        </div>
      </div>
      <div class="bulletin-wrapper" @click = "showDetail">
        <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
      <div class="background">
        <img :src="seller.avatar" width="100%" height="100%">
      </div>
      <!--商家信息-->
      <transition name="fade">
        <div v-show="detailShow" class="detail">
          <div class="detail-wrapper clearfix">
            <div class="detail-main">
              <h1 class="name">{{seller.name}}</h1>
              <div class="star-wrapper">
                <star :size="48" :score="seller.score"></star>
              </div>
              <div class="title">
                <div class="line"></div>
                <div class="text">优惠信息</div>
                <div class="line"></div>
              </div>
              <!--优惠信息列表(循环)-->
              <ul v-if="seller.supports" class="supports">
                <li class="support-item" v-for="(item,index) in seller.supports">
                  <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                  <span class="text">{{seller.supports[index].description}}</span>
                </li>
              </ul>
              <div class="title">
                <div class="line"></div>
                <div class="text">商家公告</div>
                <div class="line"></div>
              </div>
              <div class="bulletin">
                <p class="contents">{{seller.bulletin}}</p>
              </div>
            </div>
          </div>
          <!--取消的图标-->
          <div class="detail-close" @click="hideDetail">
            <i class="icon-close"></i>
          </div>
        </div>
      </transition>

    </div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star';

  // 定义组件
  export default {
      props: {
          seller: {
              type: Object
          }
      },
    data() {
      return {
          detailShow: false
      };
    },
    methods: {
      showDetail() {
          this.detailShow = true;
      },
      hideDetail() {
          this.detailShow = false;
      }
    },
      created() {
         this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      },
    components: {
          star
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">

  @import "../../common/stylus/mixin.styl";

  .header
    position: relative
    overflow: hidden
    letter-spacing: 0.5px
    color : #fff
    background: rgba(7, 17, 27, 0.5)
    .content-wrapper
       position: relative
       padding : 24px 12px 18px 24px
       font-size: 0
       .avatar
         display: inline-block
         vertical-align: top
         img
           -webkit-border-radius: 2px
           -moz-border-radius: 2px
      .content
          display: inline-block
          margin-left: 16px
          .title
             margin: 2px 0 8px 0
             .brand
                display: inline-block
                width: 30px
                height: 18px
                vertical-align: top
                bg-image ("brand")
                background-size: 30px 18px
                background-repeat: no-repeat
             .name
                 margin-left: 6px
                 font-size: 16px
                 line-height: 18px
                 font-weight: bold

          .description
             margin-bottom: 10px
             line-height: 12px
             font-size: 12px

          .support
             .icon
               display: inline-block
               vertical-align: top
               width: 12px
               height: 12px
               margin-right: 4px
               background-size: 12px 12px
               background-repeat: no-repeat
               &.decrease
                 bg-image('decrease_1')
               &.discount
                 bg-image('discount_1')
               &.guarantee
                 bg-image('guarantee_1')
               &.invoice
                 bg-image('invoice_1')
               &.special
                 bg-image('special_1')

             .text
               line-height: 12px
               font-size: 12px
      .support-count
        position: absolute
        right: 12px
        bottom: 18px
        padding: 0 8px
        height: 24px
        line-height: 24px
        background: rgba(0, 0, 0, 0.2)
        -webkit-border-radius: 14px
        -moz-border-radius: 14px
        text-align: center
        .count
            vertical-align: top
            font-size: 10px
        .icon-keyboard_arrow_right
            line-height: 24px
            font-size: 10px
            margin-left: 2px

    .bulletin-wrapper
      position: relative
      height: 28px
      line-height: 28px
      padding: 0 22px 0 12px
      background: rgba(7, 17, 27, 0.2)
      white-space: nowrap
      overflow: hidden
      -ms-text-overflow: ellipsis
      text-overflow: ellipsis
      .bulletin-title
         display: inline-block
         width: 22px
         height: 12px
         margin-top: 8px
         vertical-align: top
         line-height: 12px
         bg-image("bulletin")
         background-repeat no-repeat
         background-size: 22px 12px
      .bulletin-text
        vertical-align: top
        margin: 0 4px
        font-size: 12px
        font-weight: 200
      .icon-keyboard_arrow_right
        position: absolute
        right: 12px
        top: 8px
        font-size: 12px

    .background
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      filter: blur(10px)
      z-index: -1
    .detail
      position: fixed
      top: 0
      left: 0
      z-index: 100
      width: 100%
      height: 100%
      overflow: auto
      -webkit-backdrop-filter: blur(10px)
      background: rgba(7, 17, 27,0.8)
      &.fade-enter-active, &.fade-leave-active
          transition: opacity .5s
      &.fade-enter, &.fade-leave-to
          opacity: 0
      .detail-wrapper
        min-height: 100%
        width: 100%
        .detail-main
          margin-top: 64px
          padding-bottom: 64px
          .name
            font-size: 16px
            font-weight: 700
            line-height: 18px
            text-align: center
          .star-wrapper
             margin-top: 18px
             padding: 2px 0
             text-align: center
          .title
            display flex
            margin: 28px auto 24px auto
            width: 80%
            .line
              flex: 1
              position: relative
              top: -6px
              border-bottom: 1px solid rgba(255, 255, 255, 0.2)
            .text
               padding: 0 12px
               font-size: 14px
               font-weight: 700
           // 优惠信息
          .supports
             width: 80%
             margin: 0 auto
             .support-item
               padding: 0 12px
               margin-bottom: 12px
               font-size: 0
               &:last-child
                  margin-bottom: 0
               .icon
                 display: inline-block
                 width: 16px
                 height: 16px
                 vertical-align: top
                 margin-right: 6px
                 background-size: 16px 16px
                 background-repeat: no-repeat
                 &.decrease
                    bg-image('decrease_2')
                 &.discount
                    bg-image('discount_2')
                 &.guarantee
                    bg-image('guarantee_2')
                 &.invoice
                    bg-image('invoice_2')
                 &.special
                    bg-image('special_2')
               .text
                 line-height:12px
                 font-size: 12px
                 font-weight: 200
          // 商家公告
          .bulletin
              width: 80%
              margin 0 auto
              .contents
                  padding: 0 12px
                  font-size: 12px
                  font-weight: 200
                  line-height: 24px
      .detail-close
         position: relative
         margin: -64px auto 0 auto
         height: 32px
         width: 32px
         clear: both
         font-size: 32px




</style>



