<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span class="block positive" @click="select(2,$event)" :class="{'active':selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
      <span class="block positive" @click="select(0,$event)" :class="{'active':selectType===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
      <span class="block negative" @click="select(1,$event)" :class="{'active':selectType===1}">{{desc.negative}}<span class="count">{{negative.length}}</span></span>
    </div>
    <div class="switch" @click="toggleContent" :class="{'on':onlyContent}">
      <i class="icon-check_circle"></i>
      <span class="text">只看有内容的评论</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  const POSITIVE = 0;
  const NEGATIVE = 1;
  const ALL = 2;
   export default {
       props: {
           ratings: {
               type: Array,
               default() {
                   return [];
               }
           },
           selectType: {
               type: Number,
               default: ALL
           },
          onlyContent: {
            type: Boolean,
            default: false
          },
          desc: {
               type: Object,
               default() {
                   return {
                       all: '全部',
                      positive: '满意',
                     negative: '不满意'
                   };
               }
          }
       },
     computed: {
       positives() {
           return this.ratings.filter((rating) => {
               return rating.rateType === POSITIVE;
           });
       },
       negative() {
          return this.ratings.filter((rating) => {
              return rating.rateType === NEGATIVE;
          });
       }
     },
      methods: {
         select(type, event) {
             if (!event._constructed) {
                 return;
             }
             this.$emit('select', type);
         },
        toggleContent(event) {
          if (!event._constructed) {
            return;
          }
          this.$emit('toggle');
        }
      }
   };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  .ratingselect
     .rating-type
        margin: 0 18px
        padding: 18px 0
        font-size: 0
        border-1px(rgba(7, 17, 27, 0.1))
        .block
           display: inline-block
           padding: 8px 12px
           margin-right: 8px
           border-radius: 1px
           font-size: 12px
           color: rgb(77, 85, 93)
           &.active
              color: #fff
           .count
              margin-left: 2px
              font-size: 6px
           &.positive
              background: rgba(0, 160, 220, 0.2)
              &.active
                  background: rgb(0, 160, 220)
           &.negative
              background: rgba(77, 85, 93, 0.2)
              &.active
                  background: rgb(77, 85, 93)


     .switch
        padding: 12px 18px
        line-height: 24px
        width: 100%
        font-size: 0
        border-bottom: 1px solid rgba(7, 17, 27, 0.1)
        &.on
          .icon-check_circle
             color: #00c850
        .icon-check_circle
            display: inline-block
            vertical-align: top
            margin-right: 4px
            font-size: 24px

        .text
           display: inline-block
           vertical-align: top
           font-size: 12px
</style>



